.nav-top{
    background-color: $color5;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    margin: auto;
}

.nav-bottom{
    width: 30%;
    border-radius: 0.5rem;
    background-color: $color5;
    padding: 1rem 0;
    margin: auto;
    button{
        width: fit-content;
    }
}

.acciones{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    form{   
        margin: auto;
    }
}