h1,
h2,
h3 {
  font-family: "Courgette", cursive;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

label {
  font-size: "Ubuntu", sans-serif;
}

.container-small {
  margin: auto;
  width: 90%;
}
@media (min-width: 600px) {
  .container-small {
    width: 75%;
  }
}
@media (min-width: 1024px) {
  .container-small {
    width: 50%;
  }
}

.text-center {
  text-align: center;
}

.text-primary {
  color: #013440;
}

.text-white {
  color: white;
}

.text-black {
  color: black;
}

.formulario form {
  width: 100%;
  padding: 2rem 2rem;
  display: flex;
  flex-direction: column;
}
.formulario label {
  margin-bottom: 15px;
  position: relative;
  border-bottom: 1px solid #ddd;
}
.formulario input {
  width: 100%;
  padding: 10px 0px;
  margin-top: 20px;
  border: none;
  border-radius: 0.5rem;
  outline: none;
}
.formulario select {
  width: 100%;
  padding: 10px 0px;
  margin-top: 20px;
  border: none;
  outline: none;
  border-radius: 0.5rem;
  text-align: center;
}
.formulario input::placeholder {
  opacity: 0;
  border-radius: 0.5rem;
}
.formulario span {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(30px);
  font-size: 0.825em;
  transition-duration: 300ms;
}
.formulario label:focus-within > span,
.formulario input:not(:placeholder-shown) + span {
  color: #013440;
  transform: translateY(0px);
}
.formulario label:focus-within > span,
.formulario select:not(:placeholder-shown) + span {
  color: #013440;
  transform: translateY(0px);
}

.btn-principal {
  border: none;
  background-color: #013440;
  border-radius: 0.5rem;
  padding: 0.5rem 0;
  color: black;
}

.btn-principal:hover {
  cursor: pointer;
  background-color: #001318;
  color: white;
}

.btns-auxiliares {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: auto;
}

.btn-secundario {
  border: none;
  background-color: #659AA6;
  border-radius: 0.5rem;
  padding: 0.5rem 0;
  color: black;
  padding: 1rem 2rem;
}

.btn-secundario:hover {
  cursor: pointer;
  background-color: #53848f;
}

.error {
  background-color: #ffbcbc;
  border-left: 5px solid #cb0000;
  text-align: center;
  padding: 0.2rem 0;
  margin: 1rem 2rem;
  font-weight: 700;
}

.successful {
  background-color: #b3ff90;
  border-left: 5px solid #329f00;
  padding: 0.2rem 0;
  text-align: center;
  margin: 1rem 2rem;
  font-weight: 700;
}

.btn-volver {
  border: none;
  background-color: #cfcfcf;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
}

.btn-volver:hover {
  cursor: pointer;
  background-color: #b6b6b6;
}

.html-auxiliar {
  background-color: #B4D2D9;
}

@media (min-width: 1024px) {
  .ventana {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 20rem;
    border-radius: 0.5rem;
    padding: 4rem 0rem;
    background-color: #659AA6;
  }
}

.img-ventana {
  margin: auto;
}
.img-ventana img {
  display: none;
}
@media (min-width: 1024px) {
  .img-ventana img {
    width: 90%;
    margin: auto;
    border-radius: 0.5rem;
    display: block;
  }
}

.container-login {
  margin: auto 1rem;
}

.ventana-tab {
  margin-top: 10rem;
}
@media (min-width: 600px) {
  .ventana-tab {
    margin-top: 10rem;
  }
}
@media (min-width: 1024px) {
  .ventana-tab {
    display: none;
  }
}

.nav-top {
  background-color: #013440;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin: auto;
}

.nav-bottom {
  width: 30%;
  border-radius: 0.5rem;
  background-color: #013440;
  padding: 1rem 0;
  margin: auto;
}
.nav-bottom button {
  width: fit-content;
}

.acciones {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.acciones form {
  margin: auto;
}

.seleccion-sucursal {
  margin-top: 2rem;
}
@media (min-width: 600px) {
  .seleccion-sucursal {
    margin-top: 3rem;
  }
}
.seleccion-sucursal h1 {
  font-size: 5rem;
}
@media (min-width: 1024px) {
  .seleccion-sucursal h1 {
    font-size: 10rem;
  }
}
@media (min-width: 600px) {
  .seleccion-sucursal h1 {
    font-size: 7rem;
  }
}
.seleccion-sucursal .sucursal-contenido {
  background-color: #025159;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}
@media (min-width: 1024px) {
  .seleccion-sucursal .sucursal-contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr 5fr;
  }
}
.seleccion-sucursal .nombre {
  background-color: #013440;
  border-radius: 0.5rem;
  margin: auto;
}
.seleccion-sucursal .nombre h1 {
  margin: auto;
  font-size: 2.4rem;
}
@media (min-width: 600px) {
  .seleccion-sucursal .nombre h1 {
    font-size: 2.7rem;
    padding: 0rem 10rem;
  }
}
@media (min-width: 1024px) {
  .seleccion-sucursal .nombre h1 {
    font-size: 2.7rem;
    padding: 0rem 10rem;
  }
}
.seleccion-sucursal .info {
  margin: 0rem 2rem;
}
.seleccion-sucursal .info span {
  color: #B4D2D9;
}
.seleccion-sucursal .info form {
  display: flex;
  justify-content: flex-end;
}
@media (min-width: 600px) {
  .seleccion-sucursal .mapa {
    grid-row: 1/3;
