.ventana {
    @include desktop{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        margin-top: 20rem;
        border-radius: 0.5rem;
        padding: 4rem 0rem ;
        background-color: $color2;
    }
}

.img-ventana {
    margin: auto;
    img {
        display: none;
        @include desktop{
            width: 90%;
            margin: auto;
            border-radius: 0.5rem;
            display: block;
        }
    }
}

.container-login{
    margin: auto 1rem;
}

.ventana-tab{
    margin-top: 10rem;
    @include tablet{
        margin-top: 10rem;
    }
    @include desktop{
        display: none;
    }
}